import React from 'react';
import ReactDOM from 'react-dom';
import * as utils from './utils.js';
import Card from './Card'

class DeckOfCards extends React.Component {

    constructor() {
        super();
        this.returnCards = this.returnCards.bind(this);
        this.restart = this.restart.bind(this);
        this.drop = this.drop.bind(this);
        let deck = utils.newDeck();
        this.state = {
            deck: utils.newDeck(),
            reset: false
        };
    }

    restart() {
        console.log('restart');
        this.setState({
            reset: true
        });

        setTimeout(() => {
            this.setState({
                reset: false
            });
        }, 200);
        let holderB = document.getElementsByClassName('empty-holder-b');
        let holderR = document.getElementsByClassName('empty-holder-r');
        for (let i = 0; i < holderB.length; i++) {
            holderB[i].innerHTML = ''
        }
    }

    drop(ev) {
        ev.preventDefault();
        let data = ev.dataTransfer.getData("text");
        let elem = document.getElementById(data);

        console.log('ev.target',);
        let holderVal = ev.target.getAttribute('value');
        let dropVal = elem.getAttribute('value');
        if (holderVal == dropVal) {
            elem.style.left = 'auto';
            elem.style.top = 'auto';
            elem.style.margin = '-1px';
            ev.target.appendChild(elem);
            if (document.getElementById('cards-container').childElementCount == 0) {
                // All cards placed. Add restart button
                let restartBtn = document.createElement('button');
                restartBtn.className += 'restart-btn';
                restartBtn.innerHTML = 'Restart';
                restartBtn.addEventListener('click', this.restart);
                document.getElementById('cards-container').appendChild(restartBtn)

            }

        }


    }

    allowDrop(ev) {
        ev.preventDefault();
    }

    onDragStart(ev) {
        ev.dataTransfer.setData("text", ev.target.id);
    }

    returnCards(data, itr) {
        let card = <Card indexId={'card' + itr} onDragStart={this.onDragStart} value={data}/>;
        return (card)
    }

    componentDidMount() {
        let skip_height = $('#cards-container').height();
        $('#cards-container').ready(function () {
            $('.card-container').each(function () {

                let holder = $(this).parent();
                let divWidth = holder.width();
                let divHeight = holder.height();

                $(this).css({
                    'left': Math.floor(Math.random() * Number(divWidth)),
                    'top': (Math.floor(Math.random() * Number(divHeight))) + (skip_height / 2.5)
                });

            })
        });
    }

    render() {
        return (
            <div className="main-container">
                <div className="child-30">
                    <div className="div-by-4">
                        <div className="card-holder" id="spades-holder" onDrop={this.drop} onDragOver={this.allowDrop}>
                            {
                                this.state.reset ? null :
                                    <div className="drop-container" value="spades" onDrop={this.drop}
                                         onDragOver={this.allowDrop}>

                                    </div>
                            }

                            {
                                this.state.reset ? null :
                                    <div className="empty-holder-b"><p className="empty-text">Place spades here</p>
                                        <p>&spades;</p></div>
                            }
                        </div>
                    </div>
                    <div className="div-by-4">
                        <div className="card-holder" id="clubs-holder" onDrop={this.drop} onDragOver={this.allowDrop}>
                            {
                                this.state.reset ? null :
                                    <div className="drop-container" value="clubs" onDrop={this.drop}
                                         onDragOver={this.allowDrop}>

                                    </div>
                            }

                            {
                                this.state.reset ? null :
                                    <div className="empty-holder-b"><p className="empty-text">Place clubs here</p>
                                        <p>&clubs;</p></div>
                            }


                        </div>
                    </div>
                    <div className="div-by-4">
                        <div className="card-holder" id="hearts-holder" onDrop={this.drop} onDragOver={this.allowDrop}>

                            {
                                this.state.reset ? null :
                                    <div className="drop-container" value="hearts" onDrop={this.drop}
                                         onDragOver={this.allowDrop}>

                                    </div>
                            }

                            {
                                this.state.reset ? null :
                                    <div className="empty-holder-r"><p className="empty-text">Place hearts here</p>
                                        <p>&hearts;</p></div>
                            }

                        </div>
                    </div>
                    <div className="div-by-4">
                        <div className="card-holder" id="diamonds-holder" onDrop={this.drop}
                             onDragOver={this.allowDrop}>
                            {
                                this.state.reset ? null :
                                    <div className="drop-container" value="diamonds" onDrop={this.drop}
                                         onDragOver={this.allowDrop}>

                                    </div>
                            }
                            {
                                this.state.reset ? null :
                                    <div className="empty-holder-r"><p className="empty-text">Place diamonds here</p>
                                        <p>&diams;</p></div>
                            }


                        </div>
                    </div>

                </div>
                <div className="child-70" id="cards-container" onDrop={this.drop} onDragOver={this.allowDrop}>
                    {
                        this.state.reset ? null : (
                            this.state.deck.map(this.returnCards)
                        )
                    }

                </div>
            </div>
        )
    }

}

ReactDOM.render(
    <DeckOfCards/>
    , document.getElementById('app')
)


